(function () {
  'use strict';

  angular
    .module('ta-nsds-ui.ministry-of-interiors')
    .service('MinistryOfInteriorsService', MinistryOfInteriorsService);

  /** @ngInject */
  function MinistryOfInteriorsService($timeout, toastr) {

    this.loadLineChart = function (ipData) {
      var monthList = ['Jan', 'Feb', 'March', 'April', 'May', 'June', 'July', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'];
      var monthIpList = [];

      angular.forEach(ipData.xAxis.categories, function (val, key) {
        monthIpList.push(new Date(val).getDate() + " " + monthList[new Date(val).getMonth()]);
      });

      $('#container1').highcharts({
        title: {
          text: 'Monthly Average Response Time for Service',
          x: -20 //center
        },
        subtitle: {
          text: 'Source: Ministry of Interior',
          x: -20
        },
        xAxis: {
          categories: monthIpList
        },
        yAxis: {
          title: {
            text: 'Days'
          },
          plotLines: [{
            value: 0,
            width: 1,
            color: '#808080'
          }]
        },
        tooltip: {
          valueSuffix: ' Days'
        },
        legend: {
          layout: 'vertical',
          align: 'right',
          verticalAlign: 'middle',
          borderWidth: 0
        },
        series: ipData.series
      });
    };

    this.loadPieChart = function (ipData, str) {
      console.log(str)
      $('#container2').highcharts({
        chart: {
          type: 'pie',
          options3d: {
            enabled: true,
            alpha: 45,
            beta: 0
          }
        },
        title: {
          text: 'Nationality Wise ' + str + '\s Registered - 2015'
        },
        tooltip: {
          pointFormat: '<b>{point.percentage:.1f}%</b>'
        },
        plotOptions: {
          pie: {
            events: {
              click: function (event) {
                console.log('event : ', event);
                console.log('Category: ' + this.category + ', value: ' + this.y);
              }
            },
            allowPointSelect: true,
            cursor: 'pointer',
            depth: 35,
            dataLabels: {
              enabled: true,
              format: '{point.name}'
            }
          }
        },
        series: [{
          type: 'pie',
          name: ipData.Country,
          data: ipData.data
        }]
      });
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('ta-nsds-ui.ministry-of-interiors')
    .factory('MinistryOfInteriorsFactory', MinistryOfInteriorsFactory);

  /** @ngInject */
  function MinistryOfInteriorsFactory($resource, $q, urls) {

    var getPieChartData = function (ipObj) {
      var defer = $q.defer();

      $resource(urls.apiUrl + urls.piechart, null)
        .save({index: 'interior'}, ipObj, function (data) {
          defer.resolve(data);
        }, function (error) {
          defer.reject(error);
        });

      return defer.promise;
    };

    var getLineChartData = function (ipObj) {
      var defer = $q.defer();
      $resource(urls.apiUrl + urls.linechart, null)
        .save({index: 'interior'}, ipObj, function (response) {
          defer.resolve(response);
        }, function (error) {
          defer.reject(error);
        });
      return defer.promise;
    };

    var getMetricData = function (ipObj) {
      var defer = $q.defer();
      $resource(urls.apiUrl + urls.metric + '?index=:type', {type: 'interior'}, null)
        .save('', ipObj, function (response) {
          defer.resolve(response);
        }, function (error) {
          defer.reject(error);
        });
      return defer.promise;
    };

    return {
      getPieChartData: getPieChartData,
      getLineChartData: getLineChartData,
      getMetricData: getMetricData
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('ta-nsds-ui.ministry-of-interiors')
    .controller('MinistryOfInteriorController', MinistryOfInteriorController);

  /** @ngInject */
  function MinistryOfInteriorController($scope, $rootScope, MinistryOfInteriorsService, MinistryOfInteriorsFactory) {
    var vm = this;
    $rootScope.headerTitle = 'Ministry of Interior';

    $(function () {


      $scope.changePieTab = function (tabNo, str) {
        $scope.activePieTab = tabNo;

        var pieChart = {
          "terms": "Country$8",
          "metric": "count$",
          "filter": ["ServiceType$" + str + "$string"],
          "range": []
        };

        MinistryOfInteriorsFactory
          .getPieChartData(pieChart)
          .then(function (data) {
            MinistryOfInteriorsService.loadPieChart(data.series[0], str)
          },
          function (error) {
            console.log(error);
          });
      };

      $scope.changePieTab(1, 'EID');


      var lineChart = {
        "size": 0,
        "query": {
          "filtered": {
            "query": {
              "query_string": {
                "query": "*",
                "analyze_wildcard": true
              }
            },
            "filter": {
              "bool": {
                "must": [
                  {
                    "range": {
                      "timestamp": {
                        "gte": 1439626373477,
                        "lte": 1447402373477,
                        "format": "epoch_millis"
                      }
                    }
                  }
                ],
                "must_not": []
              }
            }
          }
        },
        "aggs": {
          "2": {
            "date_histogram": {
              "field": "timestamp",
              "interval": "10d",
              "time_zone": "Asia/Kolkata",
              "min_doc_count": 1,
              "extended_bounds": {
                "min": 1439626373477,
                "max": 1447402373477
              }
            },
            "aggs": {
              "3": {
                "terms": {
                  "field": "ServiceType",
                  "size": 5,
                  "order": {
                    "1": "desc"
                  }
                },
                "aggs": {
                  "1": {
                    "avg": {
                      "field": "ProcessTime"
                    }
                  }
                }
              }
            }
          }
        }
      };

      MinistryOfInteriorsFactory
        .getLineChartData(lineChart)
        .then(function (data) {
          MinistryOfInteriorsService.loadLineChart(data);
        },
        function (error) {
          console.log(error);
        });

      var metricDataEID = {
        "input": ["avg$ProcessTime"],
        "filter": ["ServiceType$EID$string"],
        "range": []
      };

      $scope.metric = {};

      MinistryOfInteriorsFactory
        .getMetricData(metricDataEID)
        .then(function (data) {
          $scope.metric.eid = data.avg$ProcessTime;
        },
        function (error) {
          console.log(error);
        });

      var metricDatREP = {
        "input": ["avg$ProcessTime"],
        "filter": ["ServiceType$Residency Entry Permit$string"],
        "range": []
      };

      MinistryOfInteriorsFactory
        .getMetricData(metricDatREP)
        .then(function (data) {
          $scope.metric.residencyEntry = data.avg$ProcessTime;
        },
        function (error) {
          console.log(error);
        });

      var metricDatWEP = {
        "input": ["avg$ProcessTime"],
        "filter": ["ServiceType$Work Entry Permit$string"],
        "range": []
      };

      MinistryOfInteriorsFactory
        .getMetricData(metricDatWEP)
        .then(function (data) {
          $scope.metric.workEntry = data.avg$ProcessTime;
        },
        function (error) {
          console.log(error);
        });

      var metricDatTP = {
        "input": ["avg$ProcessTime"],
        "filter": ["ServiceType$Telephone$string"],
        "range": []
      };
      MinistryOfInteriorsFactory
        .getMetricData(metricDatTP)
        .then(function (data) {
          $scope.metric.telephone = data.avg$ProcessTime;
        },
        function (error) {
          console.log(error);
        });

    });

  }
})();

